.home-container {
    position: relative;
    height: 100vh;
    overflow: hidden;
}

.home-background-image {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}

.home-background-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.home-text {
    display: flex;
    flex-direction: column;
}

.home-profile-pic {
    height: 35vh;
    width: auto;
    margin-left: 35vw;
}

.home-content {
    height: 100%;
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.home-content h1 {
    font-size: 2em;
    font-family: Minion Pro,Times,serif;
    color: white;
    text-align: center;
    margin: 0;
}
.home-content p {
    font-size: 1.3em;
    font-family: Minion Pro,Times,serif;
    color: white;
    text-align: center;
    margin: 0;
}

@media screen and (max-width: 768px) {
    .home-profile-pic {
        height: 20vh;
        margin-bottom: 10vh;
        margin-left: 0;
        order: -1;
    }

    .home-content {
        flex-direction: column;
    }

    .home-content h1 {
        font-size: 1.5em;
    }

    .home-content p {
        font-size: 1em;
    }
    
}